import { Injectable } from '@angular/core'
import { HttpClient } from '@angular/common/http'
import { LanguageService } from './language.service'
import { CountryCode } from 'libphonenumber-js'

// Interface pour les données des pays
export interface Country {
  name_fr: string
  name_en: string
  code: CountryCode
  dial_code: string
  img?: string
  firstLetter?: string
}

@Injectable({
  providedIn: 'root',
})
export class CountryService {
  private countries: Country[] | null = null

  constructor(
    private httpClient: HttpClient,
    private languageService: LanguageService
  ) {}

  // Utilisation de Promise pour gérer l'asynchronicité
  public getCountries(): Promise<Country[]> {
    return new Promise((resolve, reject) => {
      if (this.countries) {
        resolve(this.countries)
      } else {
        this.httpClient.get<Country[]>('assets/phones.json').subscribe(
          (data) => {
            data.forEach((country) => {
              country.firstLetter = this.getFirstLetter(country)
              country.img =
                'assets/img/flags/' + country.code.toLowerCase() + '.png'
            })
            this.countries = data
            resolve(data)
          },
          (err) => {
            reject(err) // Rejeter la promesse en cas d'erreur
          }
        )
      }
    })
  }

  private getFirstLetter(country: Country): string {
    let firstLetter: string
    const language = this.languageService.getLanguage()

    if (language === 'fr') {
      firstLetter = country.name_fr.charAt(0).toUpperCase()
    } else {
      firstLetter = country.name_en.charAt(0).toUpperCase()
    }

    if (firstLetter === 'Î') {
      firstLetter = 'I'
    }

    if (firstLetter === 'É') {
      firstLetter = 'E'
    }

    return firstLetter
  }
}
