import { Injectable } from '@angular/core'
import { AsYouType, parsePhoneNumberFromString } from 'libphonenumber-js' // Ajustez le chemin selon votre projet
import { Country } from './country.service' // Ajustez le chemin selon votre projet

@Injectable({
  providedIn: 'root',
})
export class PhoneUtilsService {
  constructor() {}

  public getPhoneNumberPattern(country: Country): Promise<string> {
    return new Promise((resolve, reject) => {
      if (!country) {
        reject('Country data is missing')
        return
      }

      let phoneNumber = country.dial_code + '666'
      let phone = parsePhoneNumberFromString(phoneNumber)
      for (let index = 0; index <= 16; index++) {
        phone = parsePhoneNumberFromString(phoneNumber)
        try {
          if (phone?.isValid()) {
            const fullNum = new AsYouType(country.code).input(phoneNumber)
            const numOutput = fullNum.substr(fullNum.indexOf(' ') + 1)
            resolve(numOutput.replace(/[0-9]/g, 'X'))
            break
          } else if (index === 25) {
            reject('Pattern non trouvé')
          }
        } catch (error) {
          reject('Erreur lors de la création du pattern')
        }
        phoneNumber += '6'
      }
    })
  }

  public formatPhoneNumber(inputValue: string, country: Country): string {
    const countryCode = country.dial_code
    const countryType = new AsYouType(country.code)
    countryType.input(countryCode + inputValue)
    const startWith0 = inputValue.startsWith('0')
    const regex = /^[0-9 ]+$/
    let numOutput = inputValue

    if (regex.test(inputValue)) {
      const fullNum = new AsYouType(countryType.country).input(
        countryCode + inputValue
      )
      numOutput =
        countryCode.length === 5
          ? fullNum.substr(fullNum.indexOf(' ') + 4)
          : fullNum.substr(fullNum.indexOf(' ') + 1)
    }
    return startWith0 ? inputValue.substring(1) : numOutput
  }
}
