import { HttpClient } from '@angular/common/http'
import { Component, OnInit } from '@angular/core'
import {
  ModalController,
  IonContent,
  IonLoading,
  IonList,
  IonItem,
  IonItemDivider,
  IonThumbnail,
  IonLabel,
  IonSearchbar,
} from '@ionic/angular/standalone'
import { CommonModule } from '@angular/common'
import { LanguageService } from 'src/app/services/utils/language.service'
import { CountryCode } from 'libphonenumber-js'
import { Subject } from 'rxjs'
import { debounceTime } from 'rxjs/operators'
import { TranslateModule } from '@ngx-translate/core'

interface Country {
  name_fr: string
  name_en: string
  code: CountryCode
  dial_code: string
  firstLetter?: string
  img?: string
}

interface CountryGroup {
  letter: string
  countries: Country[]
}

@Component({
  selector: 'app-countries',
  templateUrl: './countries.component.html',
  styleUrls: ['./countries.component.scss'],
  imports: [
    CommonModule,
    IonContent,
    IonLoading,
    IonList,
    IonItem,
    TranslateModule,
    IonThumbnail,
    IonLabel,
    IonSearchbar,
  ],
})
export class CountriesComponent implements OnInit {
  public isLoaded: boolean = false
  public groups: CountryGroup[] = []
  public language: string = ''
  public searchQuery: string = ''

  public totalCountriesCount: number = 0
  public displayedCountriesCount: number = 0

  private allCountries: Country[] = []
  private topCountriesCodes: CountryCode[] = [
    'FR',
    'GB',
    'DE',
    'IT',
    'ES',
    'RE',
  ]
  private filteredCountries: Country[] = []
  private searchSubject = new Subject<string>()

  constructor(
    public modalController: ModalController,
    private httpClient: HttpClient,
    private languageService: LanguageService
  ) {}

  ngOnInit() {
    this.language = this.languageService.getLanguage()
    this.httpClient.get<Country[]>('assets/phones.json').subscribe(
      (data: Country[]) => {
        this.allCountries = data
        this.totalCountriesCount = this.allCountries.length
        this.filteredCountries = [...this.allCountries]
        this.showTopCountries()
        this.isLoaded = true
      },
      (err) => {
        this.isLoaded = true
      }
    )

    this.searchSubject.pipe(debounceTime(300)).subscribe((searchQuery) => {
      this.searchQuery = searchQuery.trim()
      this.filteredCountries = this.searchQuery
        ? this.filterCountries()
        : this.getTopCountries()
      this.updateGroups()
    })
  }

  // Fonction pour afficher les 5 pays les plus populaires par défaut
  showTopCountries() {
    this.filteredCountries = this.getTopCountries()
    this.updateGroups()
  }

  // Obtenir la liste des pays les plus populaires
  private getTopCountries(): Country[] {
    return this.allCountries
      .filter((country) => this.topCountriesCodes.includes(country.code))
      .slice(0, 5)
  }

  // Fonction pour filtrer les pays selon la recherche
  private filterCountries(): Country[] {
    const query = this.searchQuery.toLowerCase()
    return this.allCountries.filter((country) => {
      const countryName =
        this.language === 'fr' ? country.name_fr : country.name_en
      return countryName.toLowerCase().includes(query)
    })
  }

  // Fonction pour mettre à jour les groupes de pays et le nombre de pays affichés
  private updateGroups() {
    this.displayedCountriesCount = this.filteredCountries.length
    this.filteredCountries.forEach((country) => {
      country.firstLetter = this.getFirstLetter(country)
      country.img = `assets/img/flags/${country.code.toLowerCase()}.png`
    })

    const headerList = this.getHeaderList(this.filteredCountries)
    this.groups = headerList.map((letter) => ({
      letter,
      countries: this.filteredCountries.filter((x) => x.firstLetter === letter),
    }))
  }

  // Fonction pour gérer la recherche
  onSearch(event: any) {
    this.searchSubject.next(event.detail.value)
  }

  // Fonction pour sélectionner un pays
  selectCountry(country: Country) {
    this.modalController.dismiss({
      selectedCountry: country,
    })
  }

  // Fonction pour obtenir la liste des premières lettres des pays
  private getHeaderList(data: Country[]): string[] {
    const headers = Array.from(
      new Set(
        data
          .map((country) => country.firstLetter)
          .filter((letter): letter is string => letter !== undefined) // Filtrer les undefined
      )
    )

    return headers.sort()
  }

  // Fonction pour obtenir la première lettre d'un pays en fonction de la langue
  private getFirstLetter(country: Country): string {
    const name = this.language === 'fr' ? country.name_fr : country.name_en
    const firstLetter = name.charAt(0).toUpperCase()

    // Simplification de la gestion des caractères spéciaux
    return firstLetter.normalize('NFD').replace(/[\u0300-\u036f]/g, '')
  }

  // Fonction pour fermer la modale sans sélection
  dismiss() {
    this.modalController.dismiss({
      selectedCountry: false,
    })
  }
}
